import Vue from 'vue'
import VueRouter from 'vue-router'

Vue.use(VueRouter)

const routes = [
    {
        path: '/',
        name: '/',
        component: () => import('../layouts/VuebaseLayout'),
        meta: {
            requiresAuth: true
        },
        children: [
            {
                path: 'home',
                name: 'home',
                component: () => import('../views/Home'),
                meta: {
                    requiresAuth: true
                }
            },
            {
                path: 'dashboard',
                name: 'dashboard',
                component: () => import('../views/Dashboard/Layout'),
                meta: {
                    requiresAuth: true
                },
                children: [
                    {
                        path: 'indicators',
                        name: 'dashboard.indicators',
                        component: () => import('../views/Dashboard/Indicators'),
                        meta: {
                            requiresAuth: true
                        }
                    },
                    {
                        path: 'backup',
                        name: 'dashboard.backup',
                        component: () => import('../views/Dashboard/Backup'),
                        meta: {
                            requiresAuth: true
                        }
                    },
                    {
                        path: 'logs',
                        name: 'dashboard.logs',
                        component: () => import('../views/Dashboard/Logs'),
                        meta: {
                            requiresAuth: true
                        }
                    }
                ]
            }
        ]
    },
    {
        path: '/login',
        name: 'login',
        component: () => import('../views/Login/Login'),
        meta: {
            requiresAuth: false
        }
    },
    {
        path: '/register',
        name: 'register',
        component: () => import('../views/Login/Register'),
        meta: {
            requiresAuth: false
        }
    },
]

export default new VueRouter({
    routes
})
